      ],
    }),

    computed: {
      ...mapState(['barColor', 'barImage', 'accessLevel', 'drawer']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          console.log(val)
          this.$store.commit('SET_DRAWER', val)
        },
      },
      computedItems () {
        const convertedItems = this.items.map(this.mapItem)
        if (this.toggleBriefView) {
          const briefItems = []

          for (let i = 0; i < convertedItems.length; i++) {
            if (convertedItems[i].brief !== undefined && convertedItems[i].brief === true) {
              briefItems.push(convertedItems[i])
            }
            if (convertedItems[i].children !== undefined && convertedItems[i].children.length > 0) {
              const briefChildren = convertedItems[i].children.filter(
                (child) => child.brief !== undefined && child.brief === true,
              )
              for (const child of briefChildren) {
                child.to = convertedItems[i].group + '/' + child.to
              }
              briefItems.push(...briefChildren)
            }
          }
          return briefItems
        } else {
          return convertedItems
        }
      },
    },

    watch: {
      '$vuetify.breakpoint.smAndDown' (val) {
        this.$emit('update:expandOnHover', !val)
      },
    },

    methods: {
      changeLanguage,
      ...mapMutations({
        setDrawer: 'SET_DRAWER',
      }),
      mapItem (item) {
        return {
          ...item,
          children: item.children ? item.children.map(this.mapItem) : undefined,
          title: this.$t(item.title),
        }
      },
